import React, { useCallback } from 'react';
import { Navbar } from '../../Components/Navbar/navbar';
import { Footer } from '../../Components/Footer/footer';

export const AboutPage = (props) => {
    const element = useCallback((node) => {
        const options = {
            root: null, // viewport
            rootMargin: "0px",
            threshold: 0.1,  // 50% viewport   callback 
        }
        
        if (node && node.nodeType === Node.ELEMENT_NODE) {
            const observer = new IntersectionObserver(entries => {
              entries.forEach(entry => {
                if (entry.isIntersecting) {
                  entry.target.classList.add('animation');
                } else {
                  entry.target.classList.remove('animation');
                }
              });
            }, options);
        
            observer.observe(node);
        }
    }, []);

    return(
        <>
            <Navbar/>        
            <div className='page'>
                <div ref={element} className='title'>About</div>
                <div ref={element} className='abouts'>
                    <div className='description'>
                      The DxD Lab is a human-computer interaction research group in the Department of Industrial Design at KAIST. DxD stands for Design, Interaction, and Data where we explore design from, with, and by data. Our mission is to build a more inclusive society by understanding the social impact of data and AI and by making technology accountable and accessible. We seek to explore where data and design intersect, with current research projects mainly lying within the subjects of digital health, inclusive AI/algorithms, and design futures.
                    </div>
                </div>
            </div>
            <Footer/>
        </>
    )
}