import React from 'react';
import { Link } from 'react-router-dom';
import './navbar.css'

export const Navbar = () => {
    return(
        <>
            <div className='navbar'>
                <div className='navbarcontent'>
                    <Link to="/" className='logo'>
                        <img className='image' src={"/images/logo.png"} alt={"logo"}/> &nbsp; Lab
                    </Link>
                    <input className="burger-check" type="checkbox" id="burger-check" /><label className="burger-icon" htmlFor="burger-check"><span className="burger-sticks"></span></label>
                    <div className='menu'>
                        <Link to="/about" className='hamburger-bar'>
                            <span>About</span>
                        </Link>                           
                        <Link to="/people" className='hamburger-bar'>
                            <span>People</span>
                        </Link>                           
                        <Link to="/project" className='hamburger-bar'>
                            <span>Project</span>
                        </Link>
                        <Link to="/publication" className='hamburger-bar'>
                            <span>Publication</span>
                        </Link>
                        <Link to="/course" className='hamburger-bar'>
                            <span>Course</span>
                        </Link>
                    </div>
                    <div className='links'>
                        <Link to="/about" className='navBtn'>
                            <span>About</span>
                        </Link>                           
                        <Link to="/people" className='navBtn'>
                            <span>People</span>
                        </Link>                           
                        <Link to="/project" className='navBtn'>
                            <span>Project</span>
                        </Link>
                        <Link to="/publication" className='navBtn'>
                            <span>Publication</span>
                        </Link>
                        <Link to="/course" className='navBtn'>
                            <span>Course</span>
                        </Link>                       
                    </div>                    
                </div>
            </div>
        </>
    )
}